<template>
  <div class="container">
    <button @click="loginWithKakao" class="btn btn-primary">Kakao  Javascript</button>


    <a @click="kakaoAccessToken()" id="cusom-login-btn" class="cursor-pointer">
      <img src="//k.kakaocdn.net/14/dn/btqCn0WEmI3/nijroPfbpCa4at5EIsjyf0/o.jpg" width="222">
    </a>


  </div>


</template>

<script>
import http from "@/common/axios.js";

// CORS  . Kakao  response
// Access-Control-Allow-Methods: GET, POST, PUT, DELETE, OPTIONS
// Access-Control-Allow-Origin: *
export default {
  methods: {
    loginWithKakao() {
      var $this = this;

      //  object  .
      // success   
      // faile    2 .
      console.log(Kakao)
      Kakao.Auth.login({
        success: function (authObj) {
          // access_token   .
          //  access_token   user info    authObj  access_token  .
          // https://developers.kakao.com/sdk/reference/js/release/Kakao.Auth.html#AuthSuccessCallback
          //   javascript  user info    .
          console.log("----1");
          console.log(authObj);

          //      . /v2/user/me
          Kakao.API.request({
            url: "/v2/user/me", //     .
            success: async function (response) {
              if (Kakao.Auth.getAccessToken()) {
                console.log("----2");
                console.log(response);
                // Kakao Login   check
                //               
                if (!response.kakao_account.has_email || !response.kakao_account.is_email_valid) {
                  alert("kakao   ,  .");
                  return;
                } else {
                  //   
                  // async, await !!
                  let result = await $this.isRegisteredUser(response.kakao_account.email);

                  console.log("----3");
                  console.log(result);

                  if (result) {
                    $this.$store.commit("SET_LOGIN", {
                      isLogin: true,
                      userName: response.properties.nickname,
                      userProfileImageUrl: response.properties.thumbnail_image,
                    });
                  } else {
                    //   
                    alert("kakao ,   .");
                    let params = {
                      userName: response.properties.nickname,
                      userEmail: response.kakao_account.email,
                    };

                    $this.$router.push({ name: "RegisterPage", params }); // params : params
                  }
                }
              }
            },
            fail: function (error) {
              console.log("----4");
              console.log(error);
            },
          });
        },
        fail: function (error) {
          console.err("----------");
          console.error(error);
        },
      });

      function kakaoLogout() {
        if (Kakao.Auth.getAccessToken()) {
          // 
          Kakao.API.request({
            //
            url: '/v1/user/unlink',
            success: function (response) {
              //console.log(response)
            },
            fail: function (error) {
              console.log(error)
            },
          })
          // 
          Kakao.Auth.setAccessToken(undefined)
          // 
          const userinfoElem = document.querySelector('#userinfo')
          if (userinfoElem) userinfoElem.value = ''
        }
      }

    },



    async isRegisteredUser(email) {
      console.log(2);
      console.log(email);

      try {
        let { data } = await http.get(`/isRegisteredUser?email=${email}`);
        console.warn(data);
        console.warn(data.result);

        if (data.result == "registered") return true;
        else return false;
      } catch (error) {
        console.log(error);
      }
    },


    kakaoAccessToken() {
      let $this = this;
      window.Kakao.Auth.login({
        success: function (authObj) {
          console.log('authObj', authObj);
          window.Kakao.Auth.setAccessToken(authObj.access_token);
          $this.kakaoLogin();
        },
        fail: function (err) {
          alert(JSON.stringify(err))
        },
      })
    },
    kakaoLogin() {
      window.Kakao.API.request({
        url: '/v2/user/me',
      })
        .then((response) => {
          let userEmail = response.kakao_account.email;
          let userName = response.properties.nickname;
          let userProfileimgUrl = response.properties.thumbnail_image;

          console.log(userEmail)
          let kakaoInfo = {
            email: userEmail,
            name: userName,
            profileImageUrl: userProfileimgUrl
          }
          this.validateServer(kakaoInfo);
        })
        .catch((error) => {
          console.log(error);
        })
    },


  },

  mounted() {
    console.log("...................???")
    // /* global Kakao */
    // if (typeof Kakao != undefined) return;

    console.log('..........................')
    // script     
    const script = document.createElement("script");

    //   Kakao  .
    script.onload = () => Kakao.init(process.env.VUE_APP_KAKAO_JAVASCRIPT_KEY);
    script.src = "https://developers.kakao.com/sdk/js/kakao.js";
    document.head.appendChild(script);
  },
  computed: {},
  watch: {},
};
</script>

