<template>
    <div class="container">
       <button @click="loginWithKakao" class="btn btn-primary">Kakao  Javascript BackEnd</button>
    </div>
 </template>
 
 
 <script>
 import http from "@/common/axios.js";
 
 export default {
    methods: {
       loginWithKakao() {
          var $this = this;
          // /* global Kakao /     .   already defined  
          Kakao.Auth.login({
             success: function (authObj) {
                console.log("----1");
                console.log(authObj);
 
                //      . ,  /v2/user/me  
                //  access_token   user info  .
                $this.getUserProfile(authObj.access_token);
             },
             fail: function (error) {
                console.error(error);
             },
          });
       },
       async getUserProfile(accessToken) {
          console.log(accessToken);
 
          // ! BackEnd    , urlencoded   . axios  json
          // @PostMapping(value="/kakaologin")
          // public HashMap<String, String> kakaoLogin(String accessToken) {
 
          // axios  : application/json
          let options = {
             headers: { "Content-Type": "application/x-www-form-urlencoded" },
          };
 
          // urlencoded  param    
          const params = new URLSearchParams();
          params.append("accessToken", accessToken);
 
          try {
             //  
             // let {response} = await http.post("/kakaologin", params, options);
             // console.log(response);
 
             let { data } = await http.post("/kakaoLogin", params, options);
             console.log(data);
 
             if (data.result == "registered") {
                this.$store.commit("SET_LOGIN", {
                   isLogin: true,
                   userName: data.nickname,
                   userProfileImageUrl: data.thumbnail_image,
                });
             } else {
                //   
                alert("kakao ,   .");
                let params = {
                   userName: data.nickname,
                   userEmail: data.email,
                };
                this.$router.push({ name: "RegisterPage", params }); // params : params
             }
          } catch (error) {
             console.log(error);
          }
       },
    },
    mounted() {
       //  if( Kakao == undefined )    typeof 
       // typeof Kakao  string "undefined"  return
       // if (typeof Kakao !== undefined) return;    , Kakao  is not defuned 
       /* global Kakao */
       if (typeof Kakao !== "undefined") return;
 
       const script = document.createElement("script");
 
       script.onload = () => Kakao.init(process.env.VUE_APP_KAKAO_JAVASCRIPT_KEY);
       script.src = "https://developers.kakao.com/sdk/js/kakao.js";
       document.head.appendChild(script);
    },
    computed: {},
    watch: {},
 };
 </script>
 
 
 <style></style>