<template>
  <div class="event-detail">
    <CommonBackground></CommonBackground>
    <CommonBanner title=""></CommonBanner>
    <div class="view-content">
      <!-- ,    -->
      <div class="container d-flex justify-content-between">
        <small class="category border rounded px-3 bg-secondary"># {{ eventDetail.category.name }}</small>
        <div v-if="getIsLogin && getIsAdminUser">
          <base-button @click="routeToUpdateView" class="h-100"></base-button>
          <base-button @click="checkDelete" class="h-100" type="danger"></base-button>
        </div>
      </div>
      <div class="container mt-3 event-detail-container d-flex flex-wrap flex-column">
        <!--  -->
        <h1 class="title mb-3 font-weight-bold">{{ eventDetail.title }}</h1>
        <!--   -->
        <div class="mt-3 d-flex">
          <div class="col-8 pl-0">
            <span class="font-weight-bold"> </span><span class="px-3">|</span>{{ eventDetail.startDate + " ~ " + eventDetail.endDate }}
          </div>
          <div class="col-4 pr-0 text-right">
            <span class="font-weight-bold"></span><span class="px-3">|</span>{{ eventDetail.registerDate }}
          </div>
        </div>
        <hr class="w-100 mb-5 mt-1" />
        <!--  -->
        <div class="content" v-html="eventDetail.content"></div>
        <hr class="w-100 mt-5 mb-0" />
        <!--  -->
        <div class="container" v-if="eventDetail.fileList.length > 0">
          <div class="mt-3 mb-3 font-weight-bold"></div>
          <div class="container border table">
            <div class="row pl-3" v-for="(file, index) in eventDetail.fileList" :key="index">
              <div>
                <span class="fileName">{{ file.fileName }}</span>
                &nbsp;&nbsp;
                <a type="button" class="btn btn-outline btn-gray btn-xs" v-bind:href="file.fileUrl" v-bind:download="file.fileName"></a>
              </div>
            </div>
          </div>
        </div>
        <hr class="w-100 mt-0 mb-0" />
      </div>
      <div class="container mt-5 d-flex justify-content-between">
        <base-button @click="$router.push({ name: 'event' })">&lt; </base-button>
        <base-button v-if="getIsLogin && isJoined" @click="checkJoinCancel" type="danger" class="">   </base-button>
        <base-button v-if="getIsLogin && !isJoined" @click="checkJoin" type="default" class="">   </base-button>
      </div>
    </div>
  </div>
</template>

<script>
import http from "@/common/axios.js";
import CommonBackground from "./components/CommonBackground.vue";
import CommonBanner from "./components/CommonBanner.vue";
import alertify from "alertifyjs";

export default {
  components: { CommonBackground, CommonBanner },
  computed: {
    eventDetail() {
      return this.$store.getters.getEventDetail;
    },
    isJoined() {
      return this.$store.getters.getIsJoined;
    },
    getIsLogin() {
      return this.$store.getters.getIsLogin;
    },
    getIsAdminUser() {
      return this.$store.getters.getIsAdminUser;
    },
  },
  methods: {
    routeToUpdateView() {
      this.$router.push({ name: "event-update" });
    },
    checkDelete() {
      var $this = this; // alertify.confirm-function() this  alertify 
      alertify.confirm(
        " ",
        "  ?",
        function () {
          // board.boardId  X
          $this.eventDelete(); // $this 
        },
        function () {
          console.log("cancel");
        }
      );
    },
    async eventDelete() {
      // detail eventId  
      try {
        let { data } = await http.delete("/events/" + this.$store.getters.getEventId);
        // console.log(data);

        if (data.result == "login") {
          this.doLogout();
        } else {
          alertify.success(" .");
          this.$router.push({ name: "event" });
        }
      } catch (error) {
        console.log(error);
        alertify.error(" .");
      }
    },
    checkJoin() {
      var $this = this; // alertify.confirm-function() this  alertify 
      alertify.confirm(
        " ",
        " ?",
        function () {
          // board.boardId  X
          $this.eventJoin(); // $this 
        },
        function () {
          console.log("cancel");
        }
      );
    },
    async eventJoin() {
      let formData = new FormData();
      formData.append("eventId", this.$store.getters.getEventId);

      try {
        let { data } = await http.post("/events/join/" + this.$store.getters.getEventId, formData);
        // console.log(data);

        if (data.result == "login") {
          this.doLogout();
        } else {
          alertify.success("  .");
          this.$router.push({ name: "event" });
        }
      } catch (error) {
        console.log(error);
        alertify.error(" .");
      }
    },
    checkJoinCancel() {
      var $this = this; // alertify.confirm-function() this  alertify 
      alertify.confirm(
        " ",
        "   ?",
        function () {
          // board.boardId  X
          $this.eventJoinCancel(); // $this 
        },
        function () {
          console.log("cancel");
        }
      );
    },
    async eventJoinCancel() {
      try {
        let { data } = await http.delete("/events/join/" + this.$store.getters.getEventId);
        // console.log(data);

        if (data.result == "login") {
          this.doLogout();
        } else {
          alertify.success("   .");
          this.$router.push({ name: "event" });
        }
      } catch (error) {
        console.log(error);
        alertify.error(" .");
      }
    },
    doLogout() {
      this.$router.push("/login");
    },
  },
};
</script>

<style scoped>
.event-detail >>> .category {
  width: max-content;
  height: max-content;
}

.event-detail >>> .content {
  min-height: 300px;
}
</style>
