export default {
    getCodeName(group_code, code) {
        switch (group_code) {
            case "001": {
                switch (code) {
                    case "001": return "";
                    case "002": return "";
                }
            } case "002": {
                switch (code) {
                    case "001": return "";
                    case "002": return "";
                }
            } case "003": {
                switch (code) {
                    case "001": return " ";
                    case "002": return " ";
                    case "003": return "";
                }
            } case "004": {
                switch (code) {
                    case "001": return "";
                    case "002": return "";
                }
            }
        }
        return -1;
    },
    makeDateStr(year, month, day, type){ // type + / . 2022/07/24 2022.11.24
        return year + type + ( ( month < 10 ) ? '0' + month : month ) + type + ( ( day < 10 ) ? '0' + day : day );
    },
    makeTimeStr(hour, minute, second, type){ // type + / . 2022/07/24 2022.11.24
        return hour + type + ( ( minute < 60 ) ? '0' + minute : minute ) + type + ( ( second < 60 ) ? '0' + second : second );
    },
    //   
    getRandomInt(min, max) {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min)) + min; // ,  
      }

}